// @import 'vars';

.Menu {
	width: 100px;
	height: auto;
	border: 1px solid var(--very-light-pink);
	border-radius: 6px;
	padding: 20px 20px 0 20px;
	background-color: var(--white);
	position: absolute;
	top: 60px;
	right: 35px;
}
.Menu ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.Menu ul li {
	text-align: end;
}
.Menu ul li:nth-child(1),
.Menu ul li:nth-child(2) {
	font-weight: bold;
}
.Menu .Menu-link {
	padding-top: 20px;
	border-top: 1px solid var(--very-light-pink);
}
// .Menu ul li:last-child a {
// 	color: var(--hospital-green);
// 	font-size: var(--sm);
// }
.Menu ul li a {
	color: var(--black);
	text-decoration: none;
	margin-bottom: 20px;
	display: inline-block;
}
.Menu .Menu-signout {
	color: var(--hospital-green);
	font-size: var(--sm);
}
.Menu ul li p {
	color: var(--black);
	text-decoration: none;
	margin-bottom: 20px;
	display: inline-block;
}
.Menu .Menu-myorder {
	opacity: 0.3;
}
